<template>
  <el-row style="background: linear-gradient(45deg, #29bdd9, #276ace);">
    <el-row class="layout-header-nav layout-center">
      <ul class="m-0 p-0">
        <li v-for="(link,index) in navList"
            :key="index"
            :ref="link.to"
            @mouseenter="enterNav(link.to)"
            @mouseleave="leaveNav(link.to)">
          <router-link :to="{name:link.to}">{{link.title}}</router-link>
        </li>
      </ul>
    </el-row>
    <el-row class="layout-header-nav2 layout-center">
      <el-tabs v-model="activeMenu" @tab-click="handleClick">
        <el-tab-pane v-for="nav in navList" :key="nav.to" :label="nav.title" :name="nav.to"></el-tab-pane>
      </el-tabs>
    </el-row>
  </el-row>
</template>
<script>

export default {
  name: 'layout-header-nav',
  components: {},
  data () {
    return {
      activeMenu: 'home',
      navList: [{
        title: '美文欣赏',
        to: 'home'
      },{
        title: '每日一刊',
        to: 'news'
      }, {
        title: '视频解析',
        to: 'video'
      }, {
        title: '内涵段子',
        to: 'joke'
      },  {
        title: '文字时钟',
        to: 'word-clock'
      }, {
        title: '有钱任性',
        to: 'author'
      }]
    }
  },
  created () {
    this.activeMenu = this.$route.name
  },
  watch: {
    '$route.name' (val) {
      this.activeMenu = val
    }
  },
  mounted () {
  },
  methods: {
    enterNav (path) {
      // console.log('进入菜单', path)
    },
    leaveNav: function (path) {
      // console.log('离开菜单', path)
    },
    handleClick () {
      console.log('click', this.activeMenu)
      this.$router.push({
        name: this.activeMenu
      })
    },
  },
}
</script>
<style lang="scss">
.layout-header-nav {

  ul {
    display: flex;
    flex: 1;
    line-height: 60px;
    margin: 0;

    li {
      position: relative;
      width: 100px;
      text-align: center;
      list-style: none;

      > a {
        display: block;
        text-decoration: none;
        color: #fff;
        font-size: 18px;
      }

      > a:hover {
        /*background-color: #44A1EE;*/
      }

      > .router-link-active {
        /*background-color: #44A1EE;*/

        &.router-link-active::after { // 三角标志
          content: '';
          position: absolute;
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-bottom: 10px solid rgba(255, 255, 255, 1);
          margin-left: -10px;
          left: 50%;
          bottom: 0;
        }
      }
    }

    li + li {
      margin-left: 20px;
    }

  }
}
.layout-header-nav2 {
  display: none;

  .el-tabs__header {
    display: flex;
    justify-content: center;
  }
  .router-menu {
    display: flex;
    width: 100%;
    max-width: 375px;
  }
}
@media screen and (max-device-width: 400px) {
  .layout-header-nav {
    display: none;
  }
  .layout-header-nav2 {
    display: block;
  }
}
</style>
